
import InlineCalendar from 'vux/src/components/inline-calendar/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import healthy from '../../lib/healthy.js';
import tools from '../../lib/tools.js';

console.log(tools.simpleDateFormat(new Date(),'yyyy-MM').split('-'))
export default {
name:'recordIndex',
props:['message'],
components:{InlineCalendar,Datetime,Toast,Previewer},
directives: {TransferDom},
data(){
let _vm = this;
return {
shieldArr:['尿常规','血脂五项','血红蛋白','肌酸激酶同工酶','高敏C反应蛋白'],
msg:'默认值',
toDay:tools.simpleDateFormat(new Date(),'yyyy-MM-dd'),
errorImg:'this.src="' + require('./assets/loading-img-error3x.png') + '"' ,
selectTimer:'',
detailMonth:[],
month:[],
records:[],
titleDate:'',
showToast:false,
toastMsg:'',
historyList:{},
newHistory:{},
downArr:['偏瘦','疑似低血压','心动过缓','血糖偏低','疑似低血压','偏低'],
upArr:['超重偏胖','肥胖','疑似高血压','轻度异常','异常','轻度异常','心动过速','血糖偏高','偏高','高'],
managerId:'',
mouthTime:'',
mouthTimeObj:{},
count: 3,
showIcon:false,
tempList: [],
imgClass:'',
options: {
getThumbBoundsFn (index) {
let thumbnail = document.querySelectorAll('.'+_vm.imgClass+'.previewer-img')[index]
let pageYScroll = window.pageYOffset || document.documentElement.scrollTop;
let rect = thumbnail.getBoundingClientRect()
return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}
},
isClickableElement: function(el) {

return false;
}
},
}
},
mounted(){
console.log(111);



this.managerId = (localStorage.getItem('md') || tools.getRequestParam('managerId') || tools.getCookie('user_id'));
if(!this.managerId || this.managerId=='undefined'){
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
return false;
}
let val = tools.simpleDateFormat(new Date(),'yyyy-MM');
this.mouthTime = val;
val = val.split('-');
this.mouthTimeObj = {year:val[0],mouth:val[1]};
this.selectTimer = tools.simpleDateFormat(new Date(),'yyyy-MM-dd');
},
watch:{
selectTimer(n,o){



this.mouthTime = n;
let date = tools.simpleDateFormat(n || new Date(),'yyyy-MM-dd');
date = date.split('-');
this.titleDate = this.formatValue(date);
this.getRecordHistory();
}
},
methods:{
sendParentMsg:function(){
this.$emit('listenToChild','我这里是子组件发出的信息')
},
getMouth(){
let vm = this;
let date = tools.simpleDateFormat(new Date(),'yyyy-MM');
this.$vux.datetime.show({
cancelText: '取消',
confirmText: '确定',
format: 'YYYY-MM',
value: date,
onConfirm (val) {
val = val.split('-');
vm.titleDate = vm.formatValue(val);  //给title赋值
let newArr = [];
newArr.push(val[0]*1);
newArr.push(val[1]*1);
vm.detailMonth = newArr;   // 给页面date框架赋值
}
})
},
getNumberMouth(time){
let vm = this;
let date = tools.simpleDateFormat(time || new Date(),'yyyy-MM-dd');
let times = tools.simpleDateFormat(time || new Date(),'yyyy-MM-dd');
date = date.split('-');
let toDay=tools.simpleDateFormat(new Date(),'yyyy-MM-dd');//获取当前日期
let managerId = (localStorage.getItem('md') || tools.getCookie('user_id'));
this.titleDate = this.formatValue(date);
let data = {"managerId": managerId,"time": times};

this.$http.axios({method:'get',url:'/deviceData/getMeasureData?managerId='+managerId+'&time='+times}).then((result) => {
result = result.data;
if(result.successful && (result.status==200)){
vm.records = result.data;
console.log(this.records);
}else{
vm.showToast = true;
vm.toastMsg = result.resultCode.message;
console.log(vm.toastMsg);
}
},(error) => {
console.log(error);
});
},
formatValue(val) {
return val[0]+'年'+val[1]+'月';
},
goBack(){
let url = sessionStorage.getItem('tempURL');
tools.next(url || '/m/healthRecords.html');
},

getRecordHistory(){
let vm = this;
this.$http.post('/deviceData/getHistoryMeasureData',{managerId:this.managerId,time:this.selectTimer}).then((result) => {
result = result.data;
if(result.resultCode && result.resultCode.code=='TRUE'){
let tempObj = {};
if(result.data){
for(let item of result.data){
if(item.name!='ECG'){
tempObj[item.day] = tempObj[item.day]?[...tempObj[item.day],item]:[item];
}
}
for(let key in tempObj){
tempObj[key] = tempObj[key].sort((a,b)=>{return a.date < b.date})
}
for(let key in tempObj){
for(let i=0;i<tempObj[key].length;i++){
if(tempObj[key][i].type=='1'){

let dataType = tempObj[key][i].dataType;
let data = JSON.parse(tempObj[key][i][dataType]);
for(let sKey in data){
tempObj[key][i].data = data[sKey];
break ;
}
}
}
}
}
vm.historyList = Object.assign(tempObj)
console.log(vm.historyList);
}else{
vm.historyList = {};
console.error('-',result);
vm.showToast = true;
vm.toastMsg = result.resultCode.message;
}
},(error) => {
console.error('---',error);
vm.showToast = true;
vm.toastMsg = '保存失败';
})
},
show(index,imgs,type){
let vm = this;
console.log(index,'-',imgs,'-',type);
this.imgClass = type;
this.tempList = imgs;
setTimeout(function(){
vm.$refs.previewer.show(index);
},300);
},
parsingArr(arr){
return arr.join('，');
},
getHHmm(str){

str = str.replace(new RegExp(/-/gm),"/");
return tools.simpleDateFormat(str,'HH:mm');
},
showIconFn(info){
let status = this.getReportVal(info);
if((this.downArr.indexOf(status)>=0) || (this.upArr.indexOf(status)>=0) || status=='数值异常' || status=='血压异常'){
return true;
}
return false;
},
showRUText(key,val){
let text = '';
let up = '阳性';
let down = '阴性';
if(key==='urine_VitC'){
text = '维生素C(VC): ' + '<span style="font-size:0.8rem;"> ' + val + ' </span> ';
if(val>-1 && val<7){
text += down;
}else{
text += up;
}
}else if(key==='urine_leukocytes'){
text = '白细胞(LEU): ' + '<span style="font-size:0.8rem;"> ' + val + ' </span> ';
if(val>-1 && val<7){
text += down;
}else{
text += up;
}
}else if(key==='urine_bilirubin'){
text = '胆红素(BIL): ' + '<span style="font-size:0.8rem;"> ' + val + ' </span> ';
if(val>-1 && val<7){
text += down;
}else{
text += up;
}
}else if(key==='urobilinogen'){
text = '尿胆原(UBG): ' + '<span style="font-size:0.8rem;"> ' + val + ' </span> ';
if(val>-1 && val<7){
text += down;
}else{
text += up;
}
}else if(key==='urine_glucose'){
text = '葡萄糖(GLU): ' + '<span style="font-size:0.8rem;"> ' + val + ' </span> ';
if(val>-1 && val<7){
text += down;
}else{
text += up;
}
}else if(key==='urine_specific_gravity'){
text = '比重(SG): ' + '<span style="font-size:0.8rem;"> ' + val + ' </span> ';
if(val > 1.03){
text += '升高';
}else if(val < 1){
text += '降低';
}else{
text += '正常';
}
}else if(key==='urine_protein'){
text = '蛋白质(PRO): ' + '<span style="font-size:0.8rem;"> ' + val + ' </span> ';
if(val>-1 && val<7){
text += down;
}else{
text += up;
}
}else if(key==='urine_ketone'){
text = '酮体(KET): ' + '<span style="font-size:0.8rem;"> ' + val + ' </span> ';
if(val>-1 && val<7){
text += down;
}else{
text += up;
}
}else if(key==='urine_occult_blood'){
text = '潜血(BLD): ' + '<span style="font-size:0.8rem;"> ' + val + ' </span> ';
if(val>-1 && val<7){
text += down;
}else{
text += up;
}
}else if(key==='urine_nitrite'){
text = '亚硝酸盐(NIT): ' + '<span style="font-size:0.8rem;"> ' + val + ' </span> ';
if(val>-1 && val<7){
text += down;
}else{
text += up;
}
}else if(key==='urine_PH'){
text = '酸碱度(pH): ' + '<span style="font-size:0.8rem;"> ' + val + ' </span> ';
if(val > 8){
text += '升高';
}else if(val < 4.6){
text += '降低';
}else{
text += '正常';
}
}
return text;
},
setReportVal(key,val){
if(key == 'urine_PH'){
return healthy.urlsReport(val, 5 , 8.5).outType;
}else if(key == 'urine_specific_gravity'){
return healthy.urlsReport(val, 1 , 1.03).outType;
}else{
return healthy.urlsReport(val, -1 , 7).outType;
}
},
getReportVal(item){
if(item.name=='BMI'){

let temp = this.trim(healthy.weightHandle(item.heValue,item.weValue).status);
return temp;
}else if(item.name=='血压'){
let temp = this.trim(healthy.bloodPressureReport(item.value.split('/')[1],item.value.split('/')[0]).status);
return temp;
}else if(item.name=='血糖'){
let temp = this.trim(healthy.bloodSugarReport(item.value).status);
return temp;
}else if(item.name=='心率'){
let temp = this.trim(healthy.heartRateReport(item.value).status);
return temp;
}else if(item.name=='血氧'){
let temp = this.trim( healthy.bloodOxygenReport(item.value).status);
return temp;
}else if(item.name=='体温'){
let temp = this.trim( healthy.tempReport(item.value).status);
return temp;
}else if(item.name=='水分'){
let temp = this.trim( healthy.wpReport(item.value).status);
return temp;
}else if(item.name=='肌肉率'){
let temp = this.trim( healthy.muscleReport(item.weight,item.value).status);
return temp;
}else if(item.name=='骨量'){
let temp = this.trim( healthy.boneReport(item.value).status);
return temp;
}else if(item.name=='蛋白量'){
let temp = this.trim( healthy.piwReport(item.weight,item.value).status);
return temp;
}else if(item.name=='代谢'){
let temp = this.trim( healthy.bmrReport(item.value).status);
return temp;
}else if(item.name=='体脂'){
let temp = this.trim( healthy.fpReport(item.value).status);
return temp;
}else if(item.name=='胆固醇'){
let temp = this.trim( healthy.cholReport(item.value).status);
return temp;
}else if(item.name=='尿酸'){
let temp = this.trim( healthy.uaReport(item.value).status);
return temp;
}else if(item.name=='血红蛋白'){
let temp = this.trim( healthy.hbReport(item.value).status);
return temp;
}else if(item.name=='糖化血红蛋白'){
let temp = this.trim( healthy.hbaReport(item.value).status);
return temp;
}else if(item.name=='超敏C反应蛋' || item.name=='高敏C反应蛋'){
let temp = this.trim( healthy.hscrpReport(item.value).status);
return temp;
}else if(item.name=='常规C反应蛋白'){
let temp = this.trim( healthy.crpReport(item.CRP).status);
return temp;
}
},
trim(str){
return str.replace(/(^\s*)|(\s*$)/g, "");
}
}
}



